import React, { Component } from 'react';
import Years from '../years';
import Facs from '../facs';
import Groups from '../groups/groups';
import FakeApi from '../../services/fake-api';

import './filter.css'

export default class Filter extends Component {

  api = new FakeApi();

  state = {
    data: {
      years: null,
      facs: null,
      groups: null,
      filteredGroups: []
    },
    isLoading: {
      years: true,
      facs: true,
      groups: true
    },
    selected: {
      year: null,
      fac: null,
      group: null
    },
    isError: false
  };

  componentDidMount() {
    this.updateYears();
    this.updateFacs();
    this.updateGroups();
  };
  
  updateYears() {
    this.api.getYears()
      .then((years) => {
        this.setState(prevState => ({
          data: {
            ...prevState.data,
            years
          },
          isLoading: {
            ...prevState.isLoading,
            years: false
          }
        }))
      });
  };

  updateFacs() {
    this.api.getFacs()
      .then((facs) => {
        this.setState(prevState => ({
          data: {
            ...prevState.data,
            facs
          },
          isLoading: {
            ...prevState.isLoading,
            facs: false
          }
        }))
      });
  };

  updateGroups() {
    this.api.getGroups()
      .then((groups) => {
        this.setState(prevState => ({
          data: {
            ...prevState.data,
            groups
          },
          isLoading: {
            ...prevState.isLoading,
            groups: false
          }
        }))
      });
  };

  filterGroups() {
    const {data: {groups}, selected: {year, fac}} = this.state;
    let filteredGroups = [];
      
    if (year && !fac) {
      filteredGroups = groups.filter(group => group.year === year)
    };

    if (!year && fac) {
      filteredGroups = groups.filter(group => group.fac === fac)
    };

    if (year && fac) {
      filteredGroups = groups.filter(group => group.year === year && group.fac === fac)
    };
    
    this.setState(prevState => ({
      data: {
        ...prevState.data,
        filteredGroups
      }
    }))
  };

  onFilterSelected = async (filter, id) => {
    if (id === this.state.selected[filter]) {
      await this.setState(prevState => ({
        selected: {
          ...prevState.selected,
          [filter]: null
        }
      }))
    } else {
        await this.setState(prevState => ({
          selected: {
            ...prevState.selected,
            [filter]: id
          }
        }))
      }
    this.filterGroups();
  };


  render() {

    const {data: {years, facs, groups, filteredGroups}, isLoading, isError, selected} = this.state;

    if (isError) {
      return <h1>error...</h1>
    };

    if (isLoading.years || isLoading.facs || isLoading.groups) {
      return <h1>loading...</h1>
    };

    return (
      <div className="col-sm-4 col-lg-3 text-center text-uppercase filter">
        <Years
          onItemSelected={(id) => this.onFilterSelected('year', id)}
          state={selected.year}
        >
        {years}
        </Years>
        <Facs
          onItemSelected={(id) => this.onFilterSelected('fac', id)}
          state={selected.fac}
        >
        {facs}
        </Facs>
        <Groups
          onItemSelected={(id) => this.props.onGroupSelected(id)}
          state={selected.group}
        >
        {filteredGroups.length ? filteredGroups : groups}
        </Groups>
      </div>
    )
  }
};
