import React, { Component } from 'react';
import MediaQuery from 'react-responsive';
import Slider from 'react-slick';
import Week from '../week';
import FakeApi from '../../services/fake-api';

import './schedule.css'

export default class Schedule extends Component {

  api = new FakeApi();

  state = {
    schedule: null
  };

  componentDidUpdate(prevProps) {
    if (this.props.group !== prevProps.group) {
      this.api.getSchedule(this.props.group)
        .then(schedule => this.setState({schedule}));
    };

  };

  render() {

    const settings = {
      arrows: false,
      dots: true,
      initialSlide: this.props.day - 1 // какая-то срань не работает слайдер тупой разработчик слайдера со своим кривым api
    }
    
    const schedule = this.state.schedule ? <Week {...this.props} schedule={this.state.schedule}/> : null;

    return(
      <div className="col-sm-8 col-lg-9 text-center schedule">
          {schedule}
        {/* <MediaQuery maxWidth={992}>
          {/* <Slider {...settings}>
            <Day id={1} {...this.props}/>
            <Day id={2} {...this.props}/>
            <Day id={3} {...this.props}/>
            <Day id={4} {...this.props}/>
            <Day id={5} {...this.props}/>
            <Day id={6} {...this.props}/>
          </Slider>
        </MediaQuery> */}
      </div>
    )
  }

}