import React, { Component } from 'react';
import Lesson from '../lesson';

export default class Day extends Component {


  render() {
    const {schedule, id, day, odd, isCurrentWeek, lesson: nowLesson} = this.props;

    const lessons = schedule.map((lesson, i) => <Lesson key={i} id={i+1} odd={odd} nowLesson={nowLesson} isCurrentWeek={isCurrentWeek}>{lesson}</Lesson>);

    const days = ['Понедельник', 'Вторник', 'Среда', 'Четверг', 'Пятница', 'Суббота'];

    return(
      <div className="col-4 mb-3">
        <h4>{days[id-1]}</h4>
        <ul className={`list-group day ${id === day ? 'today': ''}`}>
          {lessons}
        </ul>
      </div>
    )
  }

}