import React from 'react';
import FilterItem from '../filter-item'

const Facs = (props) => {
  
  return (
    <ul className="list-group col-8 col-sm-12 facs s-table">
      <FilterItem onItemSelected={(id) => {props.onItemSelected(id)}} state={props.state}>{props.children}</FilterItem>
    </ul>
  )
};

export default Facs;