import React from 'react';
import Day from '../day';

const Week = (props) => {

  const {schedule, day, lesson, odd, isCurrentWeek} = props;

  const week = schedule.map((oneDay) => {
    const key = schedule.indexOf(oneDay);

    return <Day key={key} id={key+1} day={day} lesson={lesson} odd={odd} isCurrentWeek={isCurrentWeek} schedule={schedule[key]}/>
  })

  return(
    <div className="row justify-content-around desktop">
      {week}
    </div>
  )
};

export default Week;