import React from 'react';

const Lesson = (props) => {
  
  const {children, nowLesson, id, odd, isCurrentWeek} = props;
  
  let double = false;
  let lesson;

  function doubleLesson(oddChildren, evenChildren) {

    function isLessonNow(id, odd) {return odd && id === nowLesson ? 'now-lesson' : ''};

    if (isCurrentWeek) {
      return (
        <div className="row">
          <div className="col-2">{id}</div>
          <div className="col-8">{odd ? oddChildren.title : evenChildren.title}</div>
          <div className="col-2">{odd ? oddChildren.cab : evenChildren.cab}</div>
        </div>
      )
    } else {
      return (
        <div className="row">
          <div className="col-2">{id}</div>
          <div className={`col-4 ${isLessonNow(id, odd)}`}>{oddChildren.title}<br/>{oddChildren.cab}</div>
          <div className={`col-4 ${isLessonNow(id, !odd)}`}>{evenChildren.title}<br/>{evenChildren.cab}</div>
          <div className="col-2">{'<<<'}</div>
        </div>
      )
    }
  };

  if (children) {
    if ('odd' in children || 'even' in children) {
      double = true;
      if ('odd' in children && 'even' in children) {
        lesson = doubleLesson({title: children.odd.title, cab: children.odd.cab}, {title: children.even.title, cab: children.even.cab});
      } else {
        if ('odd' in children) {
          lesson = doubleLesson({title: children.odd.title, cab: children.odd.cab}, {title: '---', cab: null});
        } else
          lesson = doubleLesson({title: '---', cab: null}, {title: children.even.title, cab: children.even.cab});
      }
    } else {
      lesson = <div className="row">
                <div className="col-2">{id}</div>
                <div className="col-8">{children.title}</div>
                <div className="col-2">{children.cab}</div>
              </div>;
    }
  } else {
    lesson = <div className="row">
              <div className="col-2">{id}</div>
              <div className="col-8">---</div>
            </div>;
  };


  return(
    <li className={`list-group-item container ${!isCurrentWeek && double ? 'double': ''} ${(isCurrentWeek || !double) && id === nowLesson ? 'now-lesson' : ''}`}>
      {lesson}
    </li>
  )

};

export default Lesson;