import React from 'react';

const FilterItem = (props) => {
  
  const { children, onItemSelected, state } = props;

  return children.map((item) => {
    const {id, name} = item;

    return (
      <li
        className={`list-group-item list-group-item-action ${state === id ? 'active' : ''}`}
        key={id}
        onClick={() => onItemSelected(id)}>
        {name}
        </li>
    );
  });
};

export default FilterItem;