import React from 'react'

import './header.css'

const Header = ({ day, lesson, onWeekTypeChange, isCurrentWeek, week: { calendar, study, odd } }) => {

  function weekButtonText(isCurrentWeek) {
    return isCurrentWeek ? 'общее расписание' : 'текущую неделю'
  };

  return (
    <header className="d-flex justify-content-between align-items-center header">
      <div className="now">
        <h4>{day} / {lesson ? `${lesson} пара` : '🎉🎉🎉'}</h4>
        <h5>[Календарная неделя: {calendar} / Учебная неделя: {study} ({odd ? 'Нечетная' : 'Четная'})]</h5>
      </div>
      <div>
        <button type="button" className="btn btn-outline-info mr-2" onClick={onWeekTypeChange}>Показать {weekButtonText(isCurrentWeek)}</button>
        <div className="btn-group" role="group">
          <button type="button" className="btn btn-info">Расписание</button>
          <div className="btn-group" role="group">
            <button id="btnGroupDrop3" type="button" className="btn btn-info dropdown-toggle" data-toggle="dropdown"></button>
            <div className="dropdown-menu dropdown-menu-right" x-placement="bottom-start">
              <a className="dropdown-item" href="#">Админ</a>
            </div>
          </div>
        </div>
      </div>
    </header>
  )
}

export default Header