import React, { Component, Fragment } from 'react';
import FakeApi from '../../services/fake-api';
import Header from '../header';
import Filter from '../filter';
import Schedule from '../schedule/schedule';


export default class App extends Component {

  api = new FakeApi();

  state = {
    day: {
      id: null,
      title: ''
    },
    time: {
      hour: null,
      minute: null
    },
    lesson: null,
    selectedGroup: null,
    week: {
      calendar: null,
      study: null,
      odd: null
    },
    isCurrentWeek: false
  };

  componentDidMount() {
    this.setState({
      day: {
        id: new Date().getDay(),
        title: new Date().toLocaleString('ru-ru', { weekday: 'long' })
      },
      time: {
        hour: new Date().getHours(),
        minute: new Date().getMinutes()
      },
      lesson: this.lessonNumber(),
      week: {
        calendar: this.getWeekNumber()
      }
    });
    this.setStudyWeek();
    setInterval(() => {
      this.setState({
        time: {
          hour: new Date().getHours(),
          minute: new Date().getMinutes()
        }
      })
    }, 60000)
  };

  componentDidUpdate(prevProps, prevState) {
    if (this.state.time !== prevState.time) {
      this.lessonNumber()
    }
  };

  // TODO: Сделать объект со временем пар, сделать сохранение в списки (Обычное расписание, праздничное)

  lessonNumber = ({ hour, minute } = this.state.time) => {

    const lessonUpdate = (lesson) => {
      if (lesson !== this.state.lesson) { this.setState({ lesson }) }
    }

    switch (hour) {
      case 8:
        lessonUpdate(1);
        break;

      case 9:
        if (minute < 30) {
          lessonUpdate(1);
        } else {
          lessonUpdate(2);
        };
        break;
      case 10:
        lessonUpdate(2);
        break;

      case 11:
        if (minute < 10) {
          lessonUpdate(2);
        } else {
          lessonUpdate(3);
        };
        break;
      case 12:
        lessonUpdate(3);
        break;

      case 13:
        if (minute < 10) {
          lessonUpdate(3);
        } else {
          lessonUpdate(4);
        };
        break;

      case 14:
        if (minute < 50) {
          lessonUpdate(4);
        } else {
          lessonUpdate(5);
        };
        break;
      case 15:
        lessonUpdate(5);
        break;

      case 16:
        if (minute < 30) {
          lessonUpdate(5);
        } else {
          lessonUpdate(null);
        };
        break;

      default:
        lessonUpdate(null);
        break;
    }
  };

  getWeekNumber(date) {
    //https://stackoverflow.com/questions/6117814/get-week-of-year-in-javascript-like-in-php

    date = date instanceof Date ? date : new Date()
    date = new Date(Date.UTC(date.getFullYear(), date.getMonth(), date.getDate()))
    date.setUTCDate(date.getUTCDate() + 4 - (date.getUTCDay() || 7))
    const yearStart = new Date(Date.UTC(date.getUTCFullYear(), 0, 1))
    const weekNo = Math.ceil((((date - yearStart) / 86400000) + 1) / 7)
    return weekNo
  }


  async setStudyWeek() {
    await this.api.getStudyStart()
      .then(date => {
        const study = this.getWeekNumber() - this.getWeekNumber(new Date(Date.parse(date))) + 1
        this.setState(prevState => ({
          week: {
            ...prevState.week,
            study
          }
        }))
      })
    this.setState(prevState => ({
      week: {
        ...prevState.week,
        odd: !!(this.state.week.study % 2)
      }
    }))
  };

  onGroupSelected = (selectedGroup) => {
    this.setState({ selectedGroup })
  };

  onWeekTypeChange = () => {
    this.setState({ isCurrentWeek: !this.state.isCurrentWeek })
  };

  render() {
    const { day: { id, title }, week, lesson, selectedGroup, isCurrentWeek } = this.state;
    return (
      <Fragment>
        <Header day={title} lesson={lesson} week={week} onWeekTypeChange={this.onWeekTypeChange} isCurrentWeek={isCurrentWeek} />
        <div className="row">
          <Filter onGroupSelected={(group) => this.onGroupSelected(group)} />
          <Schedule day={id} lesson={lesson} group={selectedGroup} odd={week.odd} isCurrentWeek={isCurrentWeek} />
        </div>
      </Fragment>
    )
  };

}